body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Arcadia";
  /* src: local("Arcadia"), url("/src/fonts/Arcadia.ttf") format("truetype"); */
  src: url(/static/media/font.9fb534b9.woff2) format("woff2"),
    url(/static/media/font.f14031b5.woff) format("woff");
  font-weight: normal;
}

.home-page {
  background: url(/static/media/florida-crossover.bdd56c58.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
}

